@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;600&display=swap);
#bg-cards {
  display: flex;
  flex-wrap: wrap;
  width: 90vw;
  justify-content: space-around;
  margin: 2vh 5vw;
}

#card {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 1%;
  width: 18%;
  border: 1px solid white;
}

#card h2 {
  margin-top: 5%;
}

#card img {
  width: 180%;
}

#card p {
  margin: 1.5%;
  text-align: center;
}

.text {
  background-color: #040b15;
}

body {
  background-image: url(/sistema-solar/static/media/background.a7257e7f.jpg);
  background-repeat: no-repeat;
}

* {
  color: white;
  font-family: 'Comfortaa', cursive;
  margin: 0;
}

.title {
  text-align: center;
  margin: 1%;
}

